export default {
    'pageName': 'studentsmsform',
    'pageLable': '',
    'student': true,
    'form': {
        'first_tel': '',
        'content': '',
        'fixed_time': '',
        'student_id': ''
    },
    'formField': [
        {
            'type': 'input',
            'label': '',
            'prop': 'first_tel',
            'field': 'first_tel'
        },
        {
            'type': 'textarea',
            'label': '',
            'prop': 'content',
            'field': 'content'
        },
        {
            'type': 'switchdatetime',
            'label': '',
            'prop': '',
            'field': 'fixed_time',
            'fieldActive': false,
            'datetype': 'datetime',
        }
    ],
    'pageTable': 'recording',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        first_tel: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 11, message: ' 1  11 ', trigger: 'blur' }
        ],
        content: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ]
    }
}


