export default {
    'pageName': 'unpayclearform',
    'pageLable': '',
    'form': {
        'order_id': '',
        'student_id': '',
        'classes_id': '',
        'times': 0,
        'amount': 0,
        'note': ''
    },
    'formField': [
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'order_no',
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'body'
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'origin_times',
            'text': ''
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'present_times',
            'text': ''
        },
        {
            'type': 'addition',
            'label': '',
            'prop': '',
            'field1': 'origin_times',
            'field2': 'present_times',
            'text': ''
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'unit_price',
            'text': ''
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'order_amount',
            'text': ''
        },
        {
            'type': 'subduction',
            'label': '',
            'prop': '',
            'field1': 'order_amount',
            'field2': 'unpay_amount',
            'text': ''
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'unpay_amount',
            'text': ''
        },
        {
            'type': 'text',
            'label': '',
            'prop': '',
            'field': 'order_remark'
        },
        {
            'type': 'numberinput',
            'label': '',
            'text': '',
            'prop': '',
            'field': 'times'
        },
        {
            'type': 'numberinput',
            'label': '',
            'text': '',
            'prop': '',
            'field': 'amount'
        },
        {
            'type': 'input',
            'label': '',
            'prop': 'note',
            'field': 'note'
        }
    ],
    'pageTable': 'clear',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        note: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256', trigger: 'blur' }
        ]
    }
}



